body {
  background-image: linear-gradient(to left, #ed4c67, #282c34);
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.app {
  background-color: gainsboro;
  min-height: 600px;
  margin: 40px;
  padding: 30px;
  border-radius: 20px;
  width: auto;
}
h1 {
  text-align: center;
  color: #ed4c67;
}
form {
  text-align: center;
  margin: 20px auto;
}
#todoForm input {
  background-color: #ed4c67;
  border: none;
  height: 50px;
  width: auto;
  padding: 0 20px;
  font-size: 15px;
  margin: 25px 25px 25px 0px;
  border-radius: 10px;
  color: white;
}
#todoForm input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}
#todoForm input:focus,
#todoForm button:focus {
  outline: none;
}

#todoForm button {
  height: 50px;
  width: auto;
  border-radius: 5px;
  border: 0;
  background-color: #282c34;
  color: aliceblue;
  cursor: pointer;
  font-weight: bolder;
}
@media only screen and(max-width:600px) {
  .app {
    margin: 20px;
  }
  body {
    background: cover/no-repeat;
  }
}
