.list {
  color: aliceblue;
  width: auto;
  font-weight: bolder;
  background-image: linear-gradient(to left, #ed4c67, #282c34);
  height: 50px;
  margin: 20px auto;
  border-radius: 10px;
}
.list p {
  padding: 10px;
  position: relative;
}
.deletebtn {
  border-radius: 50%;
  border: none;
  height: 25px;
  width: auto;
  color: #ed4c67;
  background-color: #282c34;
  right: 20px;
  position: absolute;
  cursor: pointer;
}
.deletebtn:focus {
  outline: none;
}

.list p input {
  background-color: transparent;
  border: none;
  color: #ed4c67;
  font-size: 20px;
}
.list p input:focus {
  outline: none;
}
